import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import zuhuList from '@/views/pages/zuhu/list'
import zuhuDetail from '@/views/pages/zuhu/formModel'
import zuhuAdd from '@/views/pages/zuhu/formAdd'
import zuhuRegister from '@/views/pages/zuhu/register'
import zuhuCenter from '@/views/pages/zuhu/center'
import fangdongList from '@/views/pages/fangdong/list'
import fangdongDetail from '@/views/pages/fangdong/formModel'
import fangdongAdd from '@/views/pages/fangdong/formAdd'
import fangwuhuxingList from '@/views/pages/fangwuhuxing/list'
import fangwuhuxingDetail from '@/views/pages/fangwuhuxing/formModel'
import fangwuhuxingAdd from '@/views/pages/fangwuhuxing/formAdd'
import fangwuxinxiList from '@/views/pages/fangwuxinxi/list'
import fangwuxinxiDetail from '@/views/pages/fangwuxinxi/formModel'
import fangwuxinxiAdd from '@/views/pages/fangwuxinxi/formAdd'
import kanfangshenqingList from '@/views/pages/kanfangshenqing/list'
import kanfangshenqingDetail from '@/views/pages/kanfangshenqing/formModel'
import kanfangshenqingAdd from '@/views/pages/kanfangshenqing/formAdd'
import zulinhetongList from '@/views/pages/zulinhetong/list'
import zulinhetongDetail from '@/views/pages/zulinhetong/formModel'
import zulinhetongAdd from '@/views/pages/zulinhetong/formAdd'
import shouzuxinxiList from '@/views/pages/shouzuxinxi/list'
import shouzuxinxiDetail from '@/views/pages/shouzuxinxi/formModel'
import shouzuxinxiAdd from '@/views/pages/shouzuxinxi/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'zuhuList',
			component: zuhuList
		}, {
			path: 'zuhuDetail',
			component: zuhuDetail
		}, {
			path: 'zuhuAdd',
			component: zuhuAdd
		}
		, {
			path: 'zuhuCenter',
			component: zuhuCenter
		}
		, {
			path: 'fangdongList',
			component: fangdongList
		}, {
			path: 'fangdongDetail',
			component: fangdongDetail
		}, {
			path: 'fangdongAdd',
			component: fangdongAdd
		}
		, {
			path: 'fangwuhuxingList',
			component: fangwuhuxingList
		}, {
			path: 'fangwuhuxingDetail',
			component: fangwuhuxingDetail
		}, {
			path: 'fangwuhuxingAdd',
			component: fangwuhuxingAdd
		}
		, {
			path: 'fangwuxinxiList',
			component: fangwuxinxiList
		}, {
			path: 'fangwuxinxiDetail',
			component: fangwuxinxiDetail
		}, {
			path: 'fangwuxinxiAdd',
			component: fangwuxinxiAdd
		}
		, {
			path: 'kanfangshenqingList',
			component: kanfangshenqingList
		}, {
			path: 'kanfangshenqingDetail',
			component: kanfangshenqingDetail
		}, {
			path: 'kanfangshenqingAdd',
			component: kanfangshenqingAdd
		}
		, {
			path: 'zulinhetongList',
			component: zulinhetongList
		}, {
			path: 'zulinhetongDetail',
			component: zulinhetongDetail
		}, {
			path: 'zulinhetongAdd',
			component: zulinhetongAdd
		}
		, {
			path: 'shouzuxinxiList',
			component: shouzuxinxiList
		}, {
			path: 'shouzuxinxiDetail',
			component: shouzuxinxiDetail
		}, {
			path: 'shouzuxinxiAdd',
			component: shouzuxinxiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/zuhuRegister',
		component: zuhuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
